  methods: {
    handleChange (value, selectedData) {
      this.text = selectedData.map(o => o.label).join(', ');
    },
    format (labels, selectedData) {
      const index = labels.length - 1;
      const data = selectedData[index] || false;
      if (data && data.code) {
        return labels[index] + ' - ' + data.code;
      }
      return labels[index];
    }
  }
